.feed-list {
    display: grid;
    grid-column-gap: calc(10px + 1vw);
    grid-row-gap: 5vw;
    background-color: #fff;
    color: #444;
    max-width: 960px;
    grid-auto-flow: dense;
    grid-template-columns: repeat(1,1fr);
    grid-template: 'main';
}



