.card_size_l {
    grid-column: span 4;
    grid-template-areas:
      'heading heading'
      'image image'
      'description tools'
      'author tools';
    grid-template-columns: 2fr auto;
    grid-template-rows: 2.70em -webkit-min-content auto;
    grid-template-rows: 2.70em min-content auto;
    font-size: 1.15em;
}

.card_size_l .card-tools {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-item-align: auto;
        align-self: auto;
}


