.YSText-Light {
    font-family: "YS Text";
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
}

.YSText-LightItalic {
    font-family: "YS Text";
    font-weight: 300;
    font-style: italic;
    font-stretch: normal;
}

.YSText-Regular {
    font-family: "YS Text";
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
}

.YSText-RegularItalic {
    font-family: "YS Text";
    font-weight: 400;
    font-style: italic;
    font-stretch: normal;
}

.YSText-Medium {
    font-family: "YS Text";
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
}

.YSText-MediumItalic {
    font-family: "YS Text";
    font-weight: 500;
    font-style: italic;
    font-stretch: normal;
}

.YSText-Bold {
    font-family: "YS Text";
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
}

.YSText-BoldItalic {
    font-family: "YS Text";
    font-weight: 700;
    font-style: italic;
    font-stretch: normal;
}

@font-face {
	font-family: "YS Text";
	src: url("YS Text-Light.eot");
		src: url("YS Text-Light.eot?#iefix") format("embedded-opentype"),
		     url("YS Text-Light.woff2") format("woff2"),
		     url("YS Text-Light.woff") format("woff"),
		     url("YS Text-Light.ttf") format("truetype"),
		     url("YS Text-Light.svg#YSText-Light") format("svg");
	font-weight: 300;
	font-style: normal;
	}

